
import { defineComponent } from 'vue'
import { get, post, refreshToken } from '@/utils/http'
import Dialog from 'primevue/dialog'
import InputText from 'primevue/inputtext'
import Textarea from 'primevue/textarea'
import Button from 'primevue/button'
// import Card from 'primevue/card'
export default defineComponent({
  name: 'Board',
  components: {
    // 'vue-scroll': vuescroll
    // Card
    Dialog,
    InputText,
    Textarea,
    Button
  },
  data() {
    return {
      lists: null,
      modalAdd: false,
      modalEdit: false,
      board: { _id: '' },
      gotdata: false,
      activeSlide: 0,
      name: '',
      description: '',
      listId: '',
      listname: '',
      listdesc: '',
      boardid: '',
      modalAddList: false,
    }
  },
  watch: {
    list: {
      handler: function () {
        console.log(this.lists)
      },
    },
    modalAdd: {
      handler: function () {
        if (!this.modalAdd) {
          this.listId = ''
        }
      }
    },
    // modalEdit: {
    //   handler: function () {
    //     console.log(this.taskname)
    //     if (!this.modalEdit) {
    //       this.taskname = ''
    //       this.taskdescription = ''
    //       this.taskid = ''
    //     }
    //   }
    // }
  },
  methods: {
    openAdd(event: any) {
      this.modalAdd = true
      this.listId = event.currentTarget.id
    },
    openEdit(task: any) {
      console.log(task._id)
    },
    closeAdd() {
      this.modalAdd = false
      this.name = ''
      this.description = ''
    },
    closeEdit() {
      this.modalEdit = false
    },
    createList() {
      this.modalAddList = true
    },
    createNewList() {
      if (this.board) {
        console.log(this.board._id)
        post('/v1/list', {
          name: this.listname,
          // eslint-disable-next-line @typescript-eslint/camelcase
          board_id: this.board._id
        }).then((res) => {
          if (res.insertedCount === 1) {
            this.getlisttasks()
            this.modalAddList = false
          }
        })
      }
    },
    getlisttasks() {
      get(`/v1/board/${this.$route.params.id}/lists`).then((res) => {
        if (res.error === 'access token is required') {
          try {
            post('/v1/auth/refresh-token', {
            // eslint-disable-next-line @typescript-eslint/camelcase
              refresh_token: localStorage.getItem('refresh_token')
            }).then((res) => {
              if (res.refresh_token) {
                localStorage.setItem('refresh_token', res.refresh_token)
              }
              this.$router.push('/login')
            })
          } catch (error) {
            console.log(error)
            this.$router.push('/login')
          }
        }
        res.push({ test: 'wow' })
        this.lists = res
      })
    },
    createBoard(event: any) {
      post('/v1/task', {
        name: this.name,
        desc: this.description,
        idList: this.listId
      }).then((res) => {
        console.log(res)
        this.getlisttasks()
        this.modalAdd = false
      })
      console.log(this.listId)
    }
  },
  beforeCreate: function () {
    document.body.className = 'board'
  },
  async mounted() {
    setInterval(refreshToken, 300000)
    await get(`/v1/board/${this.$route.params.id}`).then((res) => {
      if (res.status === 'failure') {
        console.error(res.error)
      }
      console.log(res)
      this.board = res
    })

    await get(`/v1/board/${this.$route.params.id}/lists`).then((res) => {
      if (res.error === 'access token is required') {
        try {
          post('/v1/auth/refresh-token', {
            // eslint-disable-next-line @typescript-eslint/camelcase
            refresh_token: localStorage.getItem('refresh_token')
          }).then((res) => {
            if (res.refresh_token) {
              localStorage.setItem('refresh_token', res.refresh_token)
            }
            this.$router.push('/login')
          })
        } catch (error) {
          console.log(error)
          this.$router.push('/login')
        }
      }
      res.push({ test: 'wow' })
      this.lists = res
    })

    this.gotdata = true
  },
})
